<script>
  import { Socket } from "../store";

  let numlock = true;

  let keys = [
    {
      cls: "div",
      txt: "/",
      alt: "/",
    },
    {
      cls: "mul",
      txt: "*",
      alt: "*",
    },
    {
      cls: "sub",
      txt: "-",
      alt: "-",
    },
    {
      cls: "num7",
      txt: "7",
      alt: "home",
    },
    {
      cls: "num8",
      txt: "8",
      alt: "up",
    },
    {
      cls: "num9",
      txt: "9",
      alt: "page_up",
    },
    {
      cls: "num4",
      txt: "4",
      alt: "left",
    },
    {
      cls: "num5",
      txt: "5",
    },
    {
      cls: "num6",
      txt: "6",
      alt: "right",
    },
    {
      cls: "num1",
      txt: "1",
      alt: "end",
    },
    {
      cls: "num2",
      txt: "2",
      alt: "down",
    },
    {
      cls: "num3",
      txt: "3",
      alt: "page_down",
    },
    {
      cls: "num0",
      txt: "0",
      alt: "insert",
    },
    {
      cls: "add",
      alt: "add",
      txt: "+",
    },
    {
      cls: "point",
      txt: ".",
      alt: "delete",
    },
    {
      cls: "enter",
      txt: "Enter",
      alt: "enter",
    },
  ];

  function emit(key) {
    if (!numlock) {
      if (!key.alt) return;
      $Socket.emit("keyboard:press", { key: key.alt });
      key.active = true;
      keys = [...keys.filter((i) => i.cls != key.cls), key];
      return;
    }
    $Socket.emit("keyboard:press", {
      key: key.txt.toLowerCase(),
    });
    key.active = true;
    keys = [...keys.filter((i) => i.cls != key.cls), key];
    return;
  }

</script>

<div class="numpad">
  <div class="numlock" class:on={numlock} on:click={() => (numlock = !numlock)}>
    Num Lock
  </div>
  {#each keys as key}
    <div
      class={key.cls}
      on:pointerdown={() => {
        emit(key);
      }}
    >
      {key.txt}
    </div>
  {/each}
</div>

<style>
  .numpad {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-template-areas:
      "numlock division multiplication subtraction"
      "numseven numeight numnine addition"
      "numfour numfive numsix addition"
      "numone numtwo numthree enter"
      "numzero numzero point enter";
    gap: 0.5rem;
    padding: 0.5rem;
    user-select: none;
  }

  .numlock {
    grid-area: numlock;
  }
  .div {
    grid-area: division;
  }
  .mul {
    grid-area: multiplication;
  }
  .sub {
    grid-area: subtraction;
  }
  .num7 {
    grid-area: numseven;
  }
  .num8 {
    grid-area: numeight;
  }
  .num9 {
    grid-area: numnine;
  }
  .num4 {
    grid-area: numfour;
  }
  .num5 {
    grid-area: numfive;
  }
  .num6 {
    grid-area: numsix;
  }
  .num1 {
    grid-area: numone;
  }
  .num2 {
    grid-area: numtwo;
  }
  .num3 {
    grid-area: numthree;
  }
  .num0 {
    grid-area: numzero;
  }
  .add {
    grid-area: addition;
  }
  .enter {
    grid-area: enter;
  }

  .numpad > * {
    border-radius: 8px;
    display: grid;
    height: 100%;
    width: 100%;
    place-items: center;
    text-align: center;
    background: black;
    color: aliceblue;
    position: relative;
  }

  .num7::after {
    content: "Home";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .num8::after {
    content: "⬆";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .num9::after {
    content: "PgUp";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .num4::after {
    content: "⬅";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .num6::after {
    content: "➡";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .num1::after {
    content: "End";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .num2::after {
    content: "⬇";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .num3::after {
    content: "PgDn";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .num0::after {
    content: "Ins";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .point::after {
    content: "Del";
    position: absolute;
    bottom: 10%;
    width: 100%;
    text-align: center;
  }

  .numlock.on {
    color: #45aa46;
  }

  .numpad > *:active {
    background-color: rgba(11, 52, 12, 0.8);
  }
</style>
