<script>

  import { Socket } from "../store";

  export let ringDuration = 1200;

  export let velocity = 120;

  let base = 75;
  let mod = 0.8;

  let playState = Array(6);
  let timers = Array(6);
  let channel = 0;

  let dot = [3, 5, 7, 9, 14, 16, 18];

  let stringPlay = [2, 3, 0, 4, 2, 0];

  let midiMap = [64, 59, 55, 50, 45, 40];

  let notes = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];

  function clickHandler(string, fret) {
    if (stringPlay[string] === fret) {
      stringPlay[string] = null;
    } else stringPlay[string] = fret;
    stringPlay = stringPlay;
  }

  let playBox;

  let pointerPressed = false;

  function sendNoteOff(string) {
    $Socket.emit("midi:note_off", {
      channel,
      note: midiMap[string] + stringPlay[string],
      velocity: 0,
    });
  }

  function sendNoteOn(string) {
    $Socket.emit("midi:note_on", {
      channel,
      note: midiMap[string] + stringPlay[string],
      velocity,
    });
  }

  function noteOn(string) {
    if (stringPlay[string] === null) return;
    sendNoteOn(string);
    playState[string] = true;
    timers[string] = setTimeout(() => {
      sendNoteOff(string);
      playState[string] = false;
    }, ringDuration);
  }

  function touchmoveHandler(e) {
    const target = document.elementFromPoint(
      e.touches[0].clientX,
      e.touches[0].clientY
    );
    if (target === playBox || !target) return;
    let string = target.getAttribute("string");
    if (string == undefined || string == null) return;
    if (playState[string]) return;
    return noteOn(+string);
  }

  function pointermoveHandler(e) {
    if (!pointerPressed) return;
    let string = e.target.getAttribute("string");
    if (string == undefined || string == null) return;
    if (playState[string]) return;
    return noteOn(+string);
  }

</script>

<div class="wrapper">
  <div class="fretboard">
    {#each Array(20) as _, fret}
      <div
        class="fret"
        {fret}
        class:first-fret={fret === 0}
        style="--fret-width:{base - fret / mod}"
        class:dot={dot.includes(fret)}
        class:octave={fret === 12}
      >
        {#each Array(6) as _, string}
          <div
            class="string"
            class:innactive={stringPlay[string] === null}
            class:isPressed={stringPlay[string] === fret}
            on:click|preventDefault={() => clickHandler(string, fret)}
            note={notes[(fret + midiMap[string]) % 12]}
            midi={fret + midiMap[string]}
          />
        {/each}
      </div>
    {/each}
  </div>

  <div
    class="play"
    bind:this={playBox}
    on:touchmove|preventDefault={touchmoveHandler}
    on:pointerdown={() => (pointerPressed = true)}
    on:pointerup={() => (pointerPressed = false)}
    on:pointermove|preventDefault={pointermoveHandler}
  >
    {#each Array(6) as _, i}
      <div
        class="vert-string"
        on:pointerdown={() => noteOn(5 - i)}
        string={5 - i}
        class:active={playState[5 - i]}
      />
    {/each}
  </div>
</div>

<style>
  :root {
    --f-size: 4px;
    --s-size: calc(100% / 28);
  }

  .play {
    height: calc(25% - 1rem);
    position: sticky;
    background: burlywood;
    margin-block: 0.5rem;
    left: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
  }

  .play::before {
    position: absolute;
    content: "";
    background-color: black;
    width: 90%;
    aspect-ratio: 1/1;
    top: 50%;
    left: 50%;
    z-index: -1;
    border-radius: 100%;
    transform: translate(-50%, -50%);
  }

  .vert-string {
    height: 100%;
    width: 1.25rem;
    background: silver;
  }

  .active {
    background-color: rgb(99, 46, 44, 0.6);
  }

  .wrapper {
    overflow-y: hidden;
    overflow-x: auto;
    width: 100%;
    padding-inline: 1rem;
    scroll-behavior: smooth;
    transform: translateZ(0);
    user-select: none;
  }

  .fretboard {
    margin: 0 auto;
    display: flex;
    flex-wrap: nowrap;
    height: 75%;
  }

  .fret {
    flex-grow: 1;
    flex-shrink: 0;
    height: 100%;
    width: calc(1px * var(--fret-width));
    display: flex;
    flex-direction: column;
    background-color: brown;
    border-inline: var(--f-size) solid gold;
    justify-content: space-evenly;
    align-items: center;
    overflow: visible;
  }

  @media (min-width: 800px), (orientation: landscape) {
    .fret {
      flex-shrink: initial;
      flex-grow: 1;
    }
    .fretboard {
      height: 65%;
    }

    .play {
      height: calc(35% - 1rem);
    }
  }

  .first-fret {
    height: 100%;
    width: 0;
    background: white;
  }

  .dot {
    position: relative;
  }
  .dot::before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    width: 75%;
    border-radius: 100%;
    aspect-ratio: 1/1;
  }

  .octave {
    position: relative;
  }

  .octave::before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, 175%);
    background: white;
    width: 75%;
    border-radius: 100%;
    aspect-ratio: 1/1;
  }

  .octave::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -275%);
    background: white;
    width: 75%;
    border-radius: 100%;
    aspect-ratio: 1/1;
  }

  .string {
    width: calc(100% + var(--f-size) * 2);
    height: var(--s-size);
    background-color: silver;
    cursor: pointer;
    z-index: 0;
    will-change: opacity;
    z-index: 1;
  }

  .innactive {
    opacity: 0.5;
  }

  .isPressed {
    position: relative;
    z-index: 1;
  }

  .isPressed::before {
    position: absolute;
    content: attr(note);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: red;
    color: white;
    text-align: center;
    width: 80%;
    display: grid;
    align-items: center;
    border-radius: 100%;
    aspect-ratio: 1/1;
  }

  .fret:first-of-type .isPressed::before {
    width: auto;
    height: 300%;
  }

  .fret:nth-child(2) .string {
    z-index: 0;
  }
</style>
