import { writable } from "svelte/store";

const MIDI = writable()
const Screen = writable()
const Socket = writable()
const Host = writable()

Screen.subscribe(()=>{
    return {
        width: document.body.clientWidth,
        height: document.body.clientHeight,
        orientation: window.matchMedia('(orientation: landscape)').matches? 'landscape' : 'portrait',
    }
})


function localStore(key, value, send=[], retrieve=[]) {
    
    if(window?.localStorage){
        const inLocal = localStorage.getItem(key)
        if(inLocal){
            value = retrieve.reduce((acc, fn) => fn(acc), inLocal)
        }
    }
    let {set, subscribe} = writable(value)
    return {
        subscribe,
        set: function (val){
            if(window?.localStorage){
                localStorage.setItem(key, send.reduce((acc, fn)=> fn(acc), val))
            }
            set(val)
        },
    }
}

const View = localStore('view', 'numpad')

export {
    MIDI,
    Screen,
    View,
    Socket,
    Host
}