<script>
import Mixer from "./components/Mixer/Mixer.svelte";
import PianoKey from "./components/PianoKey.svelte";
import Drums from "./components/Drums.svelte";
import LaunchPad from "./components/LaunchPad.svelte";
import Numpad from "./components/Numpad.svelte";
import Mouse from "./components/Mouse.svelte";
import Fretboard from "./components/Fretboard.svelte";
import Header from "./Header.svelte";
import Webcam from "./components/Webcam.svelte";
import QReader from './components/QReader.svelte'
import { onDestroy, onMount} from "svelte";
import { View, Screen, Socket, MIDI, Host } from "./store";
import ScreenCapture from "./components/ScreenCapture.svelte";

let observer
onMount(()=>{
    observer = new ResizeObserver(e=>{
        Screen.set({
            width: e[0].target.clientWidth,
            height:  e[0].target.clientHeight,
            orientation: window.matchMedia('(orientation: landscape)').matches? 'landscape' : 'portrait'
        })
    })
    observer.observe(document.body)
})

onDestroy(()=>{
    observer.unobserve(document.body)
    disconnect()
})


function disconnect() {
    $Socket.emit('client-disconnect')
    $Socket.disconnect()
}

const components = {
    numpad: Numpad,
    mouse: Mouse,
    piano: PianoKey,
    guitar: Fretboard,
    launchpad: LaunchPad,
    mixer: Mixer,
    drum: Drums,
    webcam: Webcam,
    capture: ScreenCapture,
}

const viewType ={
    midi: ["piano", "guitar", "launchpad", "mixer", "drum"],
    generic: ["numpad", "mouse"],
    receiver: ["webcam", "capture"]
}

function getViweType() {
    for(let v of Object.keys(viewType)){
        if (viewType[v].includes($View)) return v
    }
    return "generic"
}

let display = false

$: if($Socket){
    $Socket.on('connect', ()=> 
    {
        display = true
        update()
    })
    $Socket.on("exit", ()=>{display = false; disconnect(); Socket.set(null)})
    $Socket.on("disconnect", ()=>{display = false})

    $Socket.on("error", (err)=>console.log(err))
    $Socket.on('midi:update', ({list, selected})=>{
        MIDI.set({
            list,
            selected
        })
    })
    document.addEventListener("visibilitychange", ()=>{
        $Socket.disconnected? $Socket.connect() : disconnect()
    })
}


function update() {
    if($Socket) $Socket.emit('client-connect', getViweType())
}


$: $View, update()
</script>


<section>
    {#if display}    
        <Header/>
        <svelte:component this={components[$View]}/>
    {:else}
    <QReader/>
    {/if}

</section>

<style>
    section{
        height: 100%;
        display: grid;
        grid-template-rows: auto 1fr;
    }
</style>