<script>
  import { onDestroy, onMount, createEventDispatcher } from "svelte";
  import { Socket } from "../store";

  let x,
    y = [null, null];
  let pressed = false;
  let timer;
  let rightClick = 0;
  let click = 0;
  let sensitivity = 2;
  let scrollSensitivity = 0.2;

  onMount(() => {});

  onDestroy(() => {});

  function start(e) {
    x = e.touches ? e.touches[0].clientX : e.clientX;
    y = e.touches ? e.touches[0].clientY : e.clientY;
    pressed = true;
  }

  function end() {
    x = null;
    y = null;
    pressed = false;
    $Socket.emit("mouse:release");
    timer = setTimeout(() => {
      click = 0;
      rightClick = 0;
    }, 300);
  }

  function move(e) {
    if (!pressed || !x || !y) return;
    const dx = (e.clientX - x) * sensitivity;
    const dy = (e.clientY - y) * sensitivity;
    $Socket.emit("mouse:move", { x: dx, y: dy });
    x = e.clientX;
    y = e.clientY;
  }

  function touchstart(e) {
    x = e.touches ? e.touches[0].clientX : e.clientX;
    y = e.touches ? e.touches[0].clientY : e.clientY;
    if (e.touches.length == 2) return;
    pressed = true;
    clearTimeout(timer);
    if (x > e.target.offsetLeft + e.target.clientWidth * 0.6) {
      if (rightClick == 1) {
        $Socket.emit("mouse:right_click");
        rightClick = 0;
      }
      click = 0;
      rightClick = rightClick + 1;
      return;
    }
    if (click == 1) {
      click = 0;
      $Socket.emit("mouse:press");
    }
    rightClick = 0;
    click = click + 1;
  }

  function touchmove(e) {
    clearTimeout(timer);
    click = 0;
    rightClick = 0;
    if (e.touches.length == 1) return move(e.touches[0]);
    else if (e.touches.length == 2) return scrollProcessor(e.touches[0]);
  }

  function scrollProcessor(e) {
    if (!pressed || !x || !y) return;
    // const dx = (e.clientX - x) * scrollSensitivity
    const dy = (e.clientY - y) * scrollSensitivity;
    $Socket.emit("mouse:scroll", { x: 0, y: dy });
    x = e.clientX;
    y = e.clientY;
  }
</script>

<div
  class="mouse"
  class:pressed
  style="--x:{x ? x : 0}; --y:{y ? y : 0};"
  on:touchmove|preventDefault={touchmove}
  on:touchend={end}
  on:touchstart={touchstart}
/>

<style>
  .mouse {
    height: 100%;
    position: relative;
    overflow: hidden;
    padding: 0.5rem;
    background-color: rgb(0, 0, 0, 0.35);
    user-select: none;
  }

  .mouse::before {
    content: "";
    position: absolute;
    background: radial-gradient(#5456aa, #545688, #545666);
    width: 0.25rem;
    height: 0.25rem;
    border-radius: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    padding: 2rem;
    transform: translateX(calc(var(--x) * 1px - 50%))
      translateY(calc(var(--y) * 1px - 50%));
    will-change: opacity, transform;
  }

  .pressed::before {
    opacity: 0.2;
  }
</style>
