        }}
        on:pointerup|preventDefault={() => {
          if (activeKey.includes(35)) sendNoteOff(35);
          activeKey = activeKey.filter((i) => i != 35);
        }}
        on:contextmenu={(e) => e.preventDefault()}
      />
      <div
        class="kick right image"
        kit="Kick Right"
        map="36"
        class:active={activeKey.includes(36)}
        on:pointerdown|preventDefault={() => {
          if (!activeKey.includes(36)) sendNoteOn(36);
