        }}
        on:contextmenu={(e) => e.preventDefault()}
      />
      <div
        class="tom lo image"
        kit="Tom Low"
        map="45"
        class:active={activeKey.includes(45)}
        on:pointerdown|preventDefault={() => {
          if (!activeKey.includes(45)) sendNoteOn(45);
