        }}
        on:contextmenu={(e) => e.preventDefault()}
      />
      <div
        class="tom hi image"
        kit="Tom High"
        map="50"
        class:active={activeKey.includes(50)}
        on:pointerdown|preventDefault={() => {
          if (!activeKey.includes(50)) sendNoteOn(50);
