        }}
        on:contextmenu={(e) => e.preventDefault()}
      />
      <div
        class="cymbal crash mid-left down image"
        kit="Splash"
        map="55"
        class:active={activeKey.includes(55)}
        on:pointerdown|preventDefault={() => {
          if (!activeKey.includes(55)) sendNoteOn(55);
          activeKey = [...activeKey, 55];
        }}
        on:pointerup|preventDefault={() => {
          if (activeKey.includes(55)) sendNoteOff(55);
          activeKey = activeKey.filter((i) => i != 55);
        }}
        on:contextmenu={(e) => e.preventDefault()}
      />
      <div
        class="cymbal crash middle down image"
        kit="China"
        map="52"
        class:active={activeKey.includes(52)}
        on:pointerdown|preventDefault={() => {
          if (!activeKey.includes(52)) sendNoteOn(52);
          activeKey = [...activeKey, 52];
        }}
        on:pointerup|preventDefault={() => {
