        }}
        on:contextmenu={(e) => e.preventDefault()}
      />
      <div
        class="cymbal crash mid-right down image"
        kit="Crash"
        map="57"
        class:active={activeKey.includes(57)}
        on:pointerdown|preventDefault={() => {
          if (!activeKey.includes(57)) sendNoteOn(57);
          activeKey = [...activeKey, 57];
        }}
        on:pointerup|preventDefault={() => {
