      />
    </div>
  </div>
{:else}
  <div class="text">CURRENTLY THIS VIEW DOESNT SUPPORT PORTRAIT MODE</div>
{/if}

<style>
  * {
    box-sizing: border-box;
  }

  .reversed {
    transform: scaleX(-1);
  }

  .section {
    position: relative;
    display: grid;
    overflow: hidden;
    width: 100vw;
    height: 100%;
    background: #434543;
  }

  .text {
    place-self: center;
    font-size: large;
    text-align: center;
    padding: 5%;
  }

  .drum {
    position: relative;
  }

  .drum > * {
    aspect-ratio: 1.1/1;
  }

  .drum > *.active {
    transform: scale(1.1);
  }

  .cymbal {
    position: absolute;
    background-color: goldenrod;
    border-radius: 50%;
    border: 2px solid rgba(0, 0, 0, 0.4);
    transform-origin: 50% 50%;
    z-index: 5;
  }
  .hihat {
    width: 17%;
  }
  .crash {
    width: 25%;
  }

  .drum > div,
  .drum {
    display: grid;
    place-items: center;
    white-space: nowrap;
  }
  .tom {
    position: absolute;
    background-color: burlywood;
    border-radius: 50%;
    border: 5px solid silver;
    transform-origin: 50% 50%;
  }
  .kick {
    position: absolute;
    background-color: grey;
    border-radius: 50%;
    border: 5px solid rgba(0, 0, 0, 0.5);
    width: 22%;
    transform-origin: 50% 50%;
  }

  /* background */
  .image.hihat::after {
    border-radius: 50%;
    content: "";
    height: 100%;
    width: 100%;
    background-color: transparent;
    background-image: url("/img/x-hat-o.webp");
    background-repeat: no-repeat;
    background-size: 110% 110%;
    background-position-x: center;
    background-position-y: 15%;
  }
  .image.crash:not(.right):not(.mid-left)::after {
    border-radius: 50%;
    content: "";
    height: 100%;
    width: 100%;
    background-color: transparent;
    background-image: url("/img/x-crash.webp");
    background-repeat: no-repeat;
    background-size: 120% 120%;
    background-position-x: center;
    background-position-y: 25%;
  }
  .image.crash:not(.left):not(.mid-right)::after {
    border-radius: 50%;
    content: "";
    height: 100%;
    width: 100%;
    background-color: transparent;
    background-image: url("/img/x-ride.webp");
    background-repeat: no-repeat;
    background-size: 110% 110%;
    background-position-x: center;
    background-position-y: 15%;
  }
  /* .image.crash.mid-left::after{
        transform: rotate(-90deg);
    }
    .image.crash.mid-right::after{
        transform: rotate(20deg);
    } */
  .image.kick::after {
    border-radius: 50%;
    content: "";
    height: 100%;
    width: 100%;
    background-color: transparent;
    background-image: url("/img/x-kick.webp");
    background-repeat: no-repeat;
    background-size: 140% 170%;
    background-position-x: center;
    background-position-y: 40%;
  }
  .image.snare::after {
    border-radius: 50%;
    content: "";
    height: 100%;
    width: 100%;
    background-color: transparent;
    background-image: url("/img/x-snare.webp");
    background-repeat: no-repeat;
    background-size: 160% 170%;
    background-position-x: center;
    background-position-y: 30%;
    transform: rotate(180deg);
  }
  .image.tom::after {
    border-radius: 50%;
    content: "";
    height: 100%;
    width: 100%;
    background-color: transparent;
    background-image: url("/img/x-tom.webp");
    background-repeat: no-repeat;
    background-size: 140% 170%;
    background-position-x: center;
    background-position-y: 15%;
  }
  .drum > .image::after {
    filter: brightness(70%);
  }

  /* position */
  .hihat.close {
    bottom: -5%;
    left: 0%;
    z-index: 1;
  }
  .hihat.open {
    left: -5%;
    bottom: 22%;
    z-index: 2;
  }
  .crash.left {
    top: -20%;
    left: -10%;
    z-index: 3;
  }
  .crash.mid-left {
    width: 20%;
    top: -28%;
    left: 13%;
    z-index: 2;
  }
  .crash.mid-right {
    top: -30%;
    right: 19%;
    z-index: 4;
    width: 22%;
  }
  .crash.middle {
    top: -35%;
    left: 33%;
    z-index: 4;
    width: 26%;
  }
  .crash.right {
    top: -15%;
    right: -5%;
    z-index: 3;
  }

  .tom.floor {
    width: 18%;

    bottom: 0%;
    right: -2%;
    z-index: 2;
  }
  .tom.lo {
    width: 16.5%;
    top: 33%;
    right: 15%;
    z-index: 2;
  }
  .tom.med {
    width: 16%;
    top: 15%;
    right: 30%;
    z-index: 2;
  }

  .tom.hi {
    width: 15%;
    top: 28%;
    left: 28%;
    z-index: 2;
  }

  .kick.left {
    bottom: -10%;
    left: 25%;
  }
  .kick.right {
    bottom: -10%;
    right: 25%;
  }
  .snare {
    position: absolute;
    background-color: #9ab8af;
    border-radius: 50%;
    border: 5px solid #de9922;
    width: 19%;
    top: 40%;
  }
</style>
