<script>
  import { onDestroy, onMount } from "svelte";
  import { Socket } from "../store";

  let channel = 0;
  let row = 3;
  let column = 6;
  let lowestNote = 36;
  let activeKey = [];
  let velocity = 120;
  $: total = row * column;

  let pressed = false;
  let ro;
  let landscape;
  onMount(() => {
    ro = new ResizeObserver((e) => {
      landscape = window.matchMedia("(orientation: landscape)").matches;
    });
    ro.observe(document.body);
  });

  onDestroy(() => {
    ro.unobserve(document.body);
  });

  function sendNoteOff(note) {
    $Socket.emit("midi:note_off", {
      channel,
      note,
      velocity: 0,
    });
  }

  function sendNoteOn(note) {
    $Socket.emit("midi:note_on", {
      channel,
      note,
      velocity,
    });
  }

  let move;
  function moveHandler(e) {
    if (!pressed) return;
    const x = e.target;
    if (!x) return;
    // if(prevent) e.preventDefault()
    const num = +x.getAttribute("map");
    if (activeKey.includes(num) || move === num) return (move = num);
    if (move) sendNoteOff(move);
    if (num) sendNoteOn(num);
    activeKey = [...activeKey.filter((i) => i != move), num];
    move = num;
  }

  let moveList = [];
  function touchMove(e) {
    const list = e.touches;
    const ml = [];
    for (let i = 0; i < list.length; i++) {
      let el = document.elementFromPoint(list[i].clientX, list[i].clientY);
      if (!el) continue;
      let n = el.getAttribute("map");
      if (n === undefined || n === null) continue;
      ml.push(+n);
    }
    const temp = activeKey.filter((i) => moveList.includes(i));
    moveList.forEach((i) => {
      if (!ml.includes(i) && activeKey.includes(i)) sendNoteOff(i);
    });

    ml.forEach((i) => {
      if (!moveList.includes(i) && !activeKey.includes(i)) sendNoteOn(i);
    });

    moveList = ml;
    const nkey = new Set([...temp, ...moveList]);
    activeKey = [...nkey];
  }

  function downHandler() {
    pressed = true;
  }

  function upHandler() {
    pressed = false;
    activeKey = activeKey.filter((i) => i !== move);
    if (move && activeKey.includes(move)) sendNoteOff(move);
    move = null;
  }

  function touchUp(e) {
    const list = e.touches;
    const ml = [];
    for (let i = 0; i < list.length; i++) {
      let el = document.elementFromPoint(list[i].clientX, list[i].clientY);
      if (!el) continue;
      let n = +el.getAttribute("map");
      if (n === undefined || n === null) continue;
      ml.push(n);
    }
    const temp = activeKey.filter((i) => ml.includes(i));
    moveList.forEach((i) => {
      if (!ml.includes(i) && activeKey.includes(i)) sendNoteOff(i);
    });
    moveList = ml;
    const nkey = new Set([...temp, ...moveList]);
    activeKey = [...nkey];
  }

</script>

<div
  class="launcpad"
  style="--row:{landscape ? row : column}; --column:{landscape ? column : row};"
  on:pointermove={moveHandler}
  on:pointerdown={downHandler}
  on:pointerup={upHandler}
  on:touchmove={touchMove}
  on:touchend={touchUp}
  on:touchcancel={touchUp}
>
  {#each Array(total) as _, i}
    <div
      class="pad"
      class:active={activeKey.includes(lowestNote + i)}
      map={lowestNote + i}
      on:pointerdown|preventDefault={() => {
        if (!activeKey.includes(lowestNote + i)) sendNoteOn(lowestNote + i);
        activeKey = [...activeKey, lowestNote + i];
      }}
      on:pointerup|preventDefault={() => {
        if (activeKey.includes(lowestNote + i)) sendNoteOff(lowestNote + i);
        activeKey = activeKey.filter((item) => item != lowestNote + i);
      }}
      on:contextmenu={(e) => e.preventDefault()}
    />
  {/each}
</div>

<style>
  .launcpad {
    height: 100%;
    display: grid;
    grid-template-columns: repeat(var(--column), 1fr);
    grid-template-rows: repeat(var(--row), 1fr);
    gap: 0.5rem;
    padding: 0.5rem;
    user-select: none;
  }

  .pad {
    width: 100%;
    height: 100%;
    background: #545655;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
  }

  .pad::before {
    content: "";
    position: absolute;
    background: radial-gradient(#5456aa, #545688, #545666);
    width: calc(100% - 4rem);
    height: calc(100% - 4rem);
    inset: 0;
    opacity: 0;
    padding: 2rem;
    will-change: opacity;
  }

  .active::before {
    opacity: 1;
  }
</style>
