<script>
import { onMount } from "svelte";
import {View} from './store'

let fullscreen = false
let control

function fs(){
    document.documentElement.requestFullscreen()
    fullscreen = true
}

function efs() {
    document.exitFullscreen()
    fullscreen = false
}

// onMount(()=>{
//     const view = localStorage.getItem('view')
//     view? View.set(view) : View.set('numpad')
// })


    let views = {
    midiSelectorView: false, 
    genericSelectorView: false, 
    streamSelectorView: false
}


function collapseAll() {
    views = {
        midiSelectorView: false, 
        genericSelectorView: false, 
        streamSelectorView: false
    }   
}

function selectorViewHandler(view) {
    if(views[view]){
        views[view] = false
        return
    }
    collapseAll()
    views[view] = true
}


$: $View, collapseAll()

</script>

<svelte:window on:pointerup={e=>{
    const path = [...e.composedPath()]
    const menu = [...document.querySelectorAll('.menu-item')]
    const outside = menu.every(i=>!path.includes(i))
    if (outside) collapseAll()
}} />

