<style>
    
    .control{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-inline: 1rem;
    }

    .option{
        display: flex;
        gap: 1rem;
        width: max-content;
    }

    .menu-item{
        position: relative;
    }

    .submenu{
        position: absolute;
        display: flex;
        gap: 1rem;
        width: max-content;
        top: 100%;
        left: 0;
        padding: 1em;
        border-radius: 5px;
        box-shadow: 0 0 4px 2px rgb(0, 0, 0, .2);
        z-index: 100;
        background: white;
        flex-wrap: wrap;
    }

    
    .menu-item:nth-of-type(3) .submenu{
        left: -150%;
    }

    @media(min-width: 400px){
        .menu-item:nth-of-type(3) .submenu{
            left: 0;
        }
    }
</style>