<script>
  import { Socket, Host } from "../store";
  import Peer from "../store/peerjs";

  let streamVideo;
  let [host, port] = $Host.split(':')
  let peer = new Peer(undefined, {host, path:'/peer', port});
  let receiver;

  $Socket.on("stream:ready", (s) => {
    receiver = s;
  });

  $: if (receiver && streamVideo) {
    peer.call(receiver, streamVideo);
  }

  function startStream() {
    const gdmOptions = {
      video: {
        cursor: "always",
      },
      audio: {
        echoCancellation: true,
        noiseSuppression: true,
        sampleRate: 44100,
      },
    };

    navigator.mediaDevices
      .getDisplayMedia(gdmOptions)
      .then((stream) => {
        stopStream();
        streamVideo = stream;
        streamVideo
          .getTracks()
          .forEach((track) => (track.onended = stopStream));
      })
      .catch((err) => console.log(err));
  }

  function stopStream() {
    if (!streamVideo) return;
    streamVideo.getTracks().forEach((track) => {
      track.stop();
    });
    streamVideo = undefined;
    $Socket.emit("stream:end");
  }

</script>

<div class="container">
  <button on:click={() => (streamVideo ? stopStream() : startStream())}
    >{streamVideo ? "Capturing" : "Capture"}</button
  >
</div>

<style>
  .container {
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
  }
</style>
