<script>
  import { onDestroy, onMount } from "svelte";
  import { Socket } from "../store";

  export let velocity = 120;

  let channel = 0;
  let blackKeys = [1, 3, 6, 8, 10];
  let octave = 2.4;
  $: whiteKeys = 7 * octave + 1;
  $: numKeys = 12 * octave + 1;
  let lowC = 36;
  let activeKey = [];
  let notes = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"];
  let pressed = false;

  function generate() {
    let arr = [];
    let n = 0;
    for (let i = 0; i < numKeys; i++) {
      arr.push({
        _class: blackKeys.includes(i % 12) ? "blackKeys" : "key",
        _style: `--n:${n}`,
      });
      if (!blackKeys.includes(i % 12)) n++;
    }

    return arr;
  }

  let move;
  function moveHandler(e) {
    if (!pressed) return;
    const x = e.target;
    if (!x) return;
    // if(prevent) e.preventDefault()
    const num = +x.getAttribute("midi");
    if (activeKey.includes(num) || move === num) return (move = num);
    sendNoteOff(move);
    sendNoteOn(num);
    activeKey = [...activeKey.filter((i) => i != move), num];
    move = num;
  }

  let moveList = [];
  function touchMove(e) {
    if (landscape) e.preventDefault();
    const list = e.touches;
    const ml = [];
    for (let i = 0; i < list.length; i++) {
      let el = document.elementFromPoint(list[i].clientX, list[i].clientY);
      if (!el) continue;
      let n = el.getAttribute("midi");
      if (n === undefined || n === null) continue;
      ml.push(+n);
    }
    const temp = activeKey.filter((i) => moveList.includes(i));
    moveList.forEach((i) => {
      if (!ml.includes(i)) sendNoteOff(i);
    });

    ml.forEach((i) => {
      if (!moveList.includes(i) && !activeKey.includes(i)) sendNoteOn(i);
    });

    moveList = ml;
    const nkey = new Set([...temp, ...moveList]);
    activeKey = [...nkey];
  }

  function downHandler() {
    pressed = true;
  }

  function upHandler() {
    pressed = false;
    activeKey = activeKey.filter((i) => i !== move);
    if (move && activeKey.includes(move)) sendNoteOff(move);
    move = null;
  }

  function touchUp(e) {
    const list = e.touches;
    const ml = [];
    for (let i = 0; i < list.length; i++) {
      let el = document.elementFromPoint(list[i].clientX, list[i].clientY);
      if (!el) continue;
      let n = +el.getAttribute("midi");
      if (n === undefined || n === null) continue;
      ml.push(n);
    }
    const temp = activeKey.filter((i) => ml.includes(i));
    moveList.forEach((i) => {
      if (!ml.includes(i)) sendNoteOff(i);
    });
    moveList = ml;
    const nkey = new Set([...temp, ...moveList]);
    activeKey = [...nkey];
  }

  let screenWidth;
  let landscape;
  let ro;

  onMount(() => {
    ro = new ResizeObserver((e) => {
      screenWidth = e[0].target.clientWidth;
      landscape = window.matchMedia("(orientation: landscape)").matches;
    });
    ro.observe(document.body);
  });

  onDestroy(() => {
    ro.unobserve(document.body);
  });

  function sendNoteOff(note) {
    $Socket.emit("midi:note_off", {
      channel,
      note,
      velocity: 0,
    });
  }

  function sendNoteOn(note) {
    $Socket.emit("midi:note_on", {
      channel,
      note,
      velocity,
    });
  }


</script>

<div class="wrapper">
  <div
    class="piano"
    style="--whitekeys:{whiteKeys}"
    on:pointermove={moveHandler}
    on:pointerdown={downHandler}
    on:pointerup={upHandler}
    on:touchmove={touchMove}
    on:touchend={touchUp}
    on:touchstart={downHandler}
    on:touchcancel={touchUp}
  >
    {#each generate() as { _class, _style }, n}
      <div
        class={_class}
        style={_style}
        class:active={activeKey.includes(lowC + n)}
        on:pointerdown|preventDefault={() => {
          if (!activeKey.includes(lowC + n)) sendNoteOn(lowC + n);
          activeKey = [...activeKey, lowC + n];
        }}
        on:pointerup|preventDefault={() => {
          if (activeKey.includes(lowC + n)) sendNoteOff(lowC + n);
          activeKey = activeKey.filter((i) => i != lowC + n);
        }}
        on:contextmenu={(e) => e.preventDefault()}
        note={notes[(n + lowC) % 12]}
        midi={n + lowC}
      />
    {/each}
  </div>
</div>

<style>
  .wrapper {
    width: 100%;
    height: 100%;
    padding: 1rem;
    overflow-x: auto;
    overflow-y: hidden;
    position: relative;
  }

  .piano {
    --width: calc(100% - 0.5rem);
    display: flex;
    width: var(--width);
    position: relative;
    height: 100%;
    --white-key-width: calc(var(--width) / (var(--whitekeys) / 2.2));
    user-select: none;
  }

  .key {
    flex-shrink: 0;
    width: var(--white-key-width);
    height: 100%;
    background: white;
    border: 1px solid #444;
    cursor: pointer;
    position: relative;
    border-radius: 0 0 5px 5px;
  }

  .blackKeys {
    width: calc(var(--white-key-width) * 0.6);
    height: 70%;
    position: absolute;
    background: black;
    top: 0;
    left: calc(var(--n) * var(--white-key-width));
    transform: translateX(-50%);
    cursor: pointer;
    z-index: 1;
    border-radius: 0 0 5px 5px;
    border: 1px solid #999;
  }

  .piano > div::before {
    will-change: opacity;
    position: absolute;
    content: "";
    inset: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(255, 70, 70, 0.3);
    opacity: 0;
  }

  .piano > div::after {
    will-change: opacity;
    position: absolute;
    content: attr(note);
    bottom: 1rem;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: end;
    text-align: center;
    width: 100%;
    height: max-content;
    color: red;
    opacity: 0;
  }

  .piano > .active::before,
  .piano > .active::after {
    opacity: 1;
  }

  @media (min-width: 800px), (orientation: landscape) {
    .wrapper {
      height: 100%;
    }
    .piano {
      --white-key-width: calc(100% / var(--whitekeys));
    }
  }
</style>
