<script>
  import { onMount, createEventDispatcher, onDestroy } from "svelte";
  import { Socket } from "../../store";

  export let channel = 0;

  const MIDI_MAP = {
    volume: 7,
    pan: 10,
    mute: 80,
    solo: 81,
  };

  const d = createEventDispatcher();

  function map(n, in_min, in_max, out_min, out_max) {
    return ((n - in_min) * (out_max - out_min)) / (in_max - in_min) + out_min;
  }

  // Pan
  let tPan = 0;
  let trackPan;
  let pressPan = false;

  $: valuePan = Math.round(map(tPan, 0, trackPan?.clientWidth || 1, 0, 127));

  function movePan(e) {
    if (!pressPan) return;
    translatePan(e);
  }

  function translatePan(e) {
    const mixer = trackPan.parentElement.parentElement.parentElement.scrollLeft;
    let x;
    if (e.clientX + mixer < trackPan.offsetLeft) {
      x = trackPan.offsetLeft;
    } else if (e.clientX + mixer > trackPan.offsetLeft + trackPan.clientWidth) {
      x = trackPan.offsetLeft + trackPan.clientWidth;
    } else {
      x = e.clientX + mixer;
    }
    tPan = x - trackPan.offsetLeft;
  }

  // Volume
  let tVol = 0;
  let trackVol;
  let pressVol = false;

  $: valueVol = Math.round(map(tVol, 0, trackVol?.clientHeight || 1, 127, 0));

  function moveVol(e) {
    if (!pressVol) return;
    translateVol(e);
  }

  function translateVol(e) {
    const y =
      e.clientY < trackVol.offsetTop
        ? trackVol.offsetTop
        : e.clientY > trackVol.offsetTop + trackVol.clientHeight
        ? trackVol.offsetTop + trackVol.clientHeight
        : e.clientY;
    tVol = y - trackVol.offsetTop;
  }

  onMount(() => {
    Ro.observe(document.body);
    tPan = trackPan.clientWidth / 2;
    tVol = trackVol.clientHeight / 2;
  });

  onDestroy(() => {
    Ro.unobserve(document.body);
  });

  const Ro = new ResizeObserver(() => {
    tPan = Math.round(map(valuePan, 0, 127, 0, trackPan?.clientWidth));
    tVol = Math.round(map(valueVol, 127, 0, 0, trackVol?.clientHeight));
  });

  let mute = false;
  let solo = false;

  function send(map, value) {
    const data = {
      control: MIDI_MAP[map],
      value,
      channel,
    };
    return $Socket.emit("midi:cc", data);
  }

  $: send("volume", valueVol);
  $: send("pan", valuePan);
  $: send("mute", mute ? 127 : 0);
  $: send("solo", solo ? 127 : 0);

  $: console.log(valueVol, valuePan);
</script>

<div class="container">
  <div
    class="pan"
    on:pointerup={() => (pressPan = false)}
    on:pointerdown={() => (pressPan = true)}
  >
    <div
      class="pan-track"
      bind:this={trackPan}
      on:click|self|preventDefault={translatePan}
    >
      <div
        class="pan-thumb"
        style="--t:{tPan}"
        on:pointermove={movePan}
        on:touchmove|preventDefault={(e) => translatePan(e.touches[0])}
      />
    </div>
  </div>

  <div
    class="vol"
    on:pointerup={() => (pressVol = false)}
    on:pointerdown={() => (pressVol = true)}
  >
    <div class="vol-track" bind:this={trackVol} on:click|self={translateVol}>
      <div
        class="vol-thumb"
        style="--t:{tVol}"
        on:pointermove={moveVol}
        on:touchmove|preventDefault={(e) => translateVol(e.touches[0])}
      />
    </div>
  </div>

  <div class="btn-group">
    <button title="Mute" class:mute on:click={() => (mute = !mute)}>M</button>
    <button title="Solo" class:solo on:click={() => (solo = !solo)}>S</button>
  </div>
</div>

<style>
  .container {
    display: grid;
    width: calc(100% - 2rem);
    grid-template-rows: auto 1fr auto;
    user-select: none;
    height: 100%;
  }

  .pan {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    box-shadow: 0 0 4px 2px inset black;
    border-radius: 0.2rem;
    background-color: #344443;
  }

  .vol {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    box-shadow: 0 0 4px 2px inset black;
    border-radius: 0.2rem;
    padding: 1rem;
    background-color: #344443;
  }

  .vol-track {
    height: calc(100% - 3rem);
    position: relative;
    width: 0.75rem;
    background: black;
    border-radius: 1rem;
  }

  .vol-thumb {
    position: absolute;
    width: 300%;
    aspect-ratio: 1/1.5;
    background: repeating-linear-gradient(
      silver,
      silver 5px,
      rgb(99, 99, 99) 5px,
      rgb(99, 99, 96) 10px
    );
    border-radius: 0.3rem;
    top: 0;
    left: 50%;
    will-change: transform;
    box-shadow: 2px 2px 6px 2px black;
    transform: translateX(-50%) translateY(calc(var(--t) * 1px - 50%));
  }

  .btn-group {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0.5rem;
    padding: 0.5rem;
    box-shadow: 0 0 4px 2px inset black;
    border-radius: 0.2rem;
    background-color: #344443;
  }

  button {
    margin: 0;
    padding: 0.5rem;
    font-weight: 600;
    border-radius: 0.25rem;
    color: white;
    background-color: rgba(225, 225, 225, 0.2);
    box-shadow: 2px 2px 6px 2px black;
    border: 2px solid white;
    aspect-ratio: 1/1;
  }

  .mute,
  [title="Mute"]:active {
    border: 2px solid #d24;
    color: #d24;
    background-color: rgba(221, 34, 68, 0.2);
  }

  .solo,
  [title="Solo"]:active {
    color: lime;
    border: 2px solid lime;
    background-color: rgba(0, 255, 0, 0.2);
  }

  .pan-track {
    width: calc(100% - 0.5rem);
    position: relative;
    height: 0.75rem;
    background: black;
    border-radius: 1rem;
  }

  .pan-thumb {
    position: absolute;
    height: 200%;
    aspect-ratio: 1/1;
    border-radius: 100%;
    background: linear-gradient(
      45deg,
      silver,
      white 25px,
      silver 50px,
      white 10px
    );
    will-change: transform;
    box-shadow: 2px 2px 6px 2px black;
    top: 50%;
    left: 0;
    transform: translateY(-50%) translateX(calc(var(--t) * 1px - 50%));
  }

  @media (hover: hover) {
    .pan-track,
    .vol-track,
    button {
      cursor: pointer;
    }
  }
</style>
