<script>
  import { onMount } from "svelte";

  import { Socket, Host } from "../store";
  import Peer from "../store/peerjs";

  onMount(() => {
    vid.muted = true;
    navigator.mediaDevices.enumerateDevices().then(function (devices) {
      devices.forEach((device) => {
        if (device.kind == "videoinput") {
          videoDevices = [...videoDevices, device];
        }
        if (device.kind == "audioinput") {
          audioDevices = [...audioDevices, device];
        }
      });
      selectedAudio = audioDevices[0].deviceId;
      selectedVideo = videoDevices[0].deviceId;
      startStream();
    });
  });


  let vid;
  let streamVideo;
  let [host, port] = $Host.split(':')
  let peer = new Peer(undefined, {host, path:'/peer', port});
  let audioDevices = [];
  let videoDevices = [];
  let selectedAudio;
  let selectedVideo;

  let receiver;

  $Socket.on("stream:ready", (s) => {
    receiver = s;
  });

  $: if (receiver && streamVideo) {
    peer.call(receiver, streamVideo);
  }

  function startStream() {
    navigator.mediaDevices
      .getUserMedia({
        video: { deviceId: selectedVideo },
        audio: { deviceId: selectedAudio },
      })
      .then((stream) => {
        if (streamVideo) {
          streamVideo.getTracks().forEach((track) => {
            track.stop();
          });
        }
        vid.srcObject = stream;
        vid.play();

        streamVideo = stream;
      })
      .catch((err) => console.log(err));
  }


  function stopStream() {
    if (!streamVideo) return;
    streamVideo.getTracks().forEach((track) => {
      track.stop();
    });
    streamVideo = undefined;
    $Socket.emit("stream:end");
  }

  $: selectedAudio, startStream();
  $: selectedVideo, startStream();
</script>

<div class="container">
  <video bind:this={vid} on:click={()=>streamVideo? stopStream() : startStream()}>
    <track kind="captions" />
  </video>
  <div class="controls">
    <div>
      <label for="selectedAudio">Microphone</label>
      <select id="selectedAudio" bind:value={selectedAudio}>
        {#each audioDevices as device, i}
          <option value={device.deviceId}
            >{device.label || "Microphone " + (i + 1)}</option
          >
        {/each}
      </select>
    </div>
    <div>
      <label for="selectedVideo">Camera</label>
      <select id="selectedVideo" bind:value={selectedVideo}>
        {#each videoDevices as device, i}
          <option value={device.deviceId}
            >{device.label || "Camera " + (i + 1)}</option
          >
        {/each}
      </select>
    </div>
  </div>
</div>

<style>
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .container {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 1fr auto;
  }

  .controls {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .controls > div {
    display: flex;
    flex-direction: column;
  }
</style>
